import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    component: () => import(/* webpackChunkName:"login_home_welcome" */ '@/components/Login')
  },
  {
    path: '/welcome',
    component: () => import(/* webpackChunkName:"login_home_welcome" */ '@/components/Home'),
    children: [
      {
        path: '',
        component: () => import(/* webpackChunkName:"login_home_welcome" */ '@/components/Welcome')
      },
      {
        path: '/users',
        component: () => import(/* webpackChunkName:"user" */ '@/components/user/Users')
      },
      {
        path: '/roles',
        component: () => import(/* webpackChunkName:"power" */ '@/components/power/Roles')
      },
      {
        path: '/rights',
        component: () => import(/* webpackChunkName:"power" */ '@/components/power/Rights')
      },
      {
        path: '/params',
        component: () => import(/* webpackChunkName:"goods" */ '@/components/goods/Params.vue')
      },
      {
        path: '/goods',
        component: () => import(/* webpackChunkName:"goods" */ '@/components/goods/Goods')
      },
      {
        path: '/categories',
        component: () => import(/* webpackChunkName:"goods" */ '@/components/goods/Categories.vue')
      },
      {
        path: '/orders',
        component: () => import(/* webpackChunkName:"orders" */ '@/components/orders/Orders.vue')
      },
      {
        path: '/reports',
        component: () => import(/* webpackChunkName:"reports" */ '@/components/reports/Reports.vue')
      },
      {
        path: '/goods/add',
        component: () => import(/* webpackChunkName:"goods" */ '@/components/goods/Add')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  // console.log(to)
  const token = localStorage.getItem('token')

  // 判断是否在 login 页面 如果在则不需要判断token 不在则判断token是否存在，并作拦截
  if (to.path === '/login') {
    next()
  } else {
    if (!token) {
      next('/login')
    } else {
      next()
    }
  }
})

export default router
