import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/assets/css/global.css'
import '@/assets/fonts/iconfont.css'
/* 引入element ui */
// import ElementUI from 'element-ui'
// import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'
import breaks from '@/Componentss/break'
import ZkTable from 'vue-table-with-tree-grid'
import NProgress from 'nprogress'
// import '../node_modules/nprogress/nprogress.css'
// 过滤器
// import dayjs from 'dayjs'

Vue.component(ZkTable.name, ZkTable)

Vue.component('breaks', breaks)

// Vue.use(ElementUI)

Vue.config.productionTip = false

Vue.prototype.$http = axios

// 本地的请求地址
// axios.defaults.baseURL = 'http://192.168.68.103:8888/api/private/v1/'
// 局域网接口
// axios.defaults.baseURL = 'http://192.168.68.23:8080/api/private/v1/'

axios.defaults.baseURL = 'https://www.liulongbin.top:8888/api/private/v1/'

// 添加请求拦截器
axios.interceptors.request.use(function (config) {
  // 携带请求头
  config.headers.Authorization = localStorage.getItem('token')
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})

// 请求在到达服务器之前，先会调用use中的这个回调函数来添加请求头信息
axios.interceptors.request.use(config => {
  // 当进入request拦截器，表示发送了请求，我们就开启进度条
  NProgress.start()
  // 为请求头对象，添加token验证的Authorization字段
  config.headers.Authorization = window.sessionStorage.getItem('token')
  // 必须返回config
  return config
})

// 在response拦截器中，隐藏进度条
axios.interceptors.response.use(config => {
  // 当进入response拦截器，表示请求已经结束，我们就结束进度条
  NProgress.done()
  return config
})

// 过滤器
Vue.filter('msgDate', msg => {
  return dayjs(msg).format('YYYY-MM-DD HH:mm:ss')
})
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
